'use client'

import Image from 'next/image'
import { Star } from 'lucide-react'

export default function ProfessionalCard({ professional }) {
  return (
    <div className="border rounded-2xl p-4 shadow-sm hover:shadow-md transition-all bg-white">
      {/* Top section: image + basic info */}
      <div className="flex gap-4">
        <Image
          src={professional.image || '/default-user.png'}
          alt={professional.name}
          width={80}
          height={80}
          className="rounded-xl object-cover"
        />
        <div className="flex-1">
          <h3 className="text-base font-semibold text-gray-800">{professional.name}</h3>
          <p className="text-sm text-gray-500">{professional.skill || 'Service Provider'}</p>

          <div className="flex items-center mt-1">
            <Star className="w-4 h-4 text-yellow-500 fill-yellow-400" />
            <span className="ml-1 text-sm text-gray-600">
              {professional.rating || '4.5'} ★
            </span>
            <span className="ml-2 text-xs text-gray-400">
              ({professional.reviews || '120'} reviews)
            </span>
          </div>
        </div>
      </div>

      {/* Middle: short description */}
      <p className="text-sm text-gray-600 mt-3 line-clamp-2">
        {professional.description || 'Experienced and reliable professional offering top-quality service.'}
      </p>

      {/* Bottom: price + button */}
      <div className="flex justify-between items-center mt-4">
        <p className="text-sm font-semibold text-pink-600">
          ₹{professional.price || '250'} / hour
        </p>
        <button className="px-3 py-1.5 text-sm rounded-lg bg-pink-500 text-white hover:bg-pink-600 transition-all">
          View Profile
        </button>
      </div>
    </div>
  )
}
